#topArrow {
  animation: fade 2.5s infinite;
}

#middleArrow {
  animation: fadeDelayOne 2.5s infinite;
}

#bottomArrow {
  animation: fadeDelayTwo 2.5s infinite;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  16% {
    opacity: 1;
  }
  32% {
    opacity: 1;
  }
  48% {
    opacity: 1;
  }
  68% {
    opacity: 0;
  }
  84% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeDelayOne {
  0% {
    opacity: 0;
  }
  16% {
    opacity: 0;
  }
  32% {
    opacity: 1;
  }
  48% {
    opacity: 1;
  }
  68% {
    opacity: 1;
  }
  84% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeDelayTwo {
  0% {
    opacity: 0;
  }
  16% {
    opacity: 0;
  }
  32% {
    opacity: 0;
  }
  48% {
    opacity: 1;
  }
  68% {
    opacity: 1;
  }
  84% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

