.title {
  /*font-size: min(72px,12vmin);*/
  font-size: min(32px,3vmin);
}

.title {
  font-family: "Goldplay", monospace;
  /*text-align: center;*/
  min-height: 1em !important;
  background: -webkit-linear-gradient(1deg, rgba(187,71,255,1) 40%, rgba(102,71,255,1) 60%);
  background: linear-gradient(1deg, rgba(187,71,255,1) 40%, rgba(102,71,255,1) 60%);
  /*background: -webkit-linear-gradient(1deg, rgb(255, 192, 92) 40%, rgb(255, 142, 71) 60%);*/
  /*background: linear-gradient(1deg, rgb(255, 192, 92) 40%, rgb(255, 142, 71) 60%);*/
  background-size: 100% 200%;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient 5s ease infinite;
  width: 100%;
}

@keyframes gradient {
  0% {
    background-position: 50% 30%;
  }
  50% {
    background-position: 50% 70%;
  }
  100% {
    background-position: 50% 30%;
  }
}

.menuItem, .menuName {
  margin: 10px;
  margin-right: 12px;
  font-size: max(min(32px,2vw),18px);
  cursor: pointer;
}

.menuItem, .menuName, .footerItem {
  font-family: "Goldplay", monospace;
  text-align: center;
  background: -webkit-linear-gradient(1deg, rgba(187,71,255,1) 65%, rgba(102,71,255,1) 85%);
  background: linear-gradient(1deg, rgba(187,71,255,1) 65%, rgba(102,71,255,1) 85%);
  /*background: -webkit-linear-gradient(1deg, rgb(255, 192, 92) 45%, rgb(255, 142, 71) 85%);*/
  /*background: linear-gradient(1deg, rgb(255, 192, 92) 45%, rgb(255, 142, 71) 85%);*/
  background-size: 100% 200%;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  border-width: 0px;
  border-bottom-width: 1px;
  border-radius: 3px;
  border-color: rgba(0,0,0,0);
  border-style: solid;
}


.menuItem:hover, .footerItem:hover {
  transform: scale(0.99);
  border-color: rgba(187,71,255,1);
  /*border-color: rgb(255, 142, 71);*/
}

