html {
  scroll-behavior: smooth;
}

.App {
  display: flex;
  flex-direction: column;
  background-color: white;
  /*overflow: hidden;*/
  margin: 0 !important;
  padding: 0 !important;
}

/* Creating of the old time-y lighting effect */
.background {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 1;
  opacity: 0.8;
  pointer-events: none; /* allow click thru */
  /*background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 55%, rgba(178, 146, 113, 0.3) 100%);*/
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 55%, rgba(149, 113, 178, 0.3) 100%);
}

.activeBackground {
  opacity: 1;
  /*background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 50%, rgba(178, 146, 113, 0.38) 100%);*/
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 45%, rgba(178, 146, 113, 0.8) 100%);
  -webkit-animation: ease flicker 2s infinite;
  animation: ease flicker 2s infinite;
}

@keyframes flicker {
  0% { opacity: 0.3; }
  10% { opacity: 0.4; }
  20% { opacity: 0.25; }
  30% { opacity: 0.5; }
  40% { opacity: 0.75; }
  50% { opacity: 0.5; }
  60% { opacity: 1; }
  70% { opacity: 0.75 }
  80% { opacity: 0.5; }
  90% { opacity: 0.25; }
  100% { opacity: 0.5; }
}

/* Creation of grain-y effect in the background */
.grain {
  background-image: url(data:image/png;base64,);
  /*background-image: url(data:image/png;base64,);*/
  height: 200vh;
  width: 200vw;
  position: fixed;
  top: -50%;
  left: -50%;
  z-index: 1;
  opacity: 1;
  pointer-events: none; /* allow click thru */
}

.activeGrain {
  -webkit-animation: grain 5s steps(6) infinite;
  animation:grain 5s steps(6) infinite;
}

@keyframes grain {
  0%, 100% {transform: translate(0,0);}
  10% {transform: translate(-5%,-10%);}
  30% {transform: translate(3%,-15%);}
  50% {transform: translate(12%,9%);}
  70% {transform: translate(9%,4%);}
  90% {transform: translate(-1%,7%);}
}

/* Creating of film damage effect */

.filmScratchInner, .filmScratchOuter {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 1;
  pointer-events: none; /* allow click thru */
}

.filmScratchInner:after, .filmScratchOuter:after {
  content: '';
  width: 120%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding-left: 100px;
  opacity: .03;
  /*background-image: url("data:image/png;base64,");*/
  /*background-image: url("data:image/png;base64,");*/
  /*background-image: url("data:image/png;base64,");*/
  /*background-image: url("data:image/png;base64,");*/
  background-image: url("data:image/png;base64,");
}

.activeScratchInner:after, .activeScratchOuter:after {
  -webkit-animation: scratch 0.45s steps(1) infinite;
  animation: scratch 0.45s steps(1) infinite;
}

.activeScratchInner:after {
  left: 30%;
  -webkit-animation: innerScratch 2s infinite;
  animation: innerScratch 2s infinite;
}

@keyframes scratch {
  0%, 100% {
    transform: translateX(0);
    opacity: 0.150;
  }

  10% {
    transform: translateX(-1%);
  }

  20% {
    transform: translateX(1%);
  }

  30% {
    transform: translateX(-2%);
    opacity: 0.18;
  }

  40% {
    transform: translateX(3%);
  }

  50% {
    transform: translateX(-3%);
    opacity: 0.10;
  }

  60% {
    transform: translateX(8%);
  }

  70% {
    transform: translateX(-3%);
  }

  80% {
    transform: translateX(10%);
    opacity: 0.04;
  }

  90% {
    transform: translateX(-2%);
  }
}

@keyframes innerScratch {
  0% {
    transform: translateX(0);
    opacity: 0.16;
  }

  10% {
    transform: translateX(-1%);
  }

  20% {
    transform: translateX(1%);
  }

  30% {
    transform: translateX(-2%);
  }

  40% {
    transform: translateX(3%);
  }

  50% {
    transform: translateX(-3%);
    opacity: 0.12;
  }

  60% {
    transform: translateX(8%);
  }

  70% {
    transform: translateX(-3%);
  }

  80% {
    transform: translateX(10%);
    opacity: 0.06;
  }

  90% {
    transform: translateX(20%);
  }

  100% {
    transform: translateX(30%);
  }
}